<template>
  <div class="mine">
    <div class="top_bg">
      <img :src="userInfo.userBg" class="auto-img" alt="" />
      <van-uploader :after-read="upLoad"  class="loadBg"/>
    </div>

    <div class="content">
      <div class="top clearfix">
        <div class="left fl">
          <img :src="userInfo.userImg" class="auto-img" alt="" />
        </div>
        <div class="right fl">
          <div class="name">{{ userInfo.nickName }}</div>
          <div class="info">
            {{ userInfo.desc == "" ? "该用户未编辑信息" : userInfo.desc }}
          </div>
        </div>
      </div>
      <div class="bottom">
        <van-cell
          v-for="(item, index) in list"
          :key="index"
          :title="item.title"
          is-link

          @click="goPage(item.name)"
        >
        </van-cell>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/mine.less";
export default {
  name: "Mine",
  data() {
    return {
      list: [
        { title: "个人资料",name:'Account' },
        { title: "我的订单",name:'Order' },
        { title: "我的收藏",name:'Like' },
        { title: "收货地址",name:'Address' },
        { title: "安全中心",name:'Secure' },
      ],
      userInfo: [],
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    getUserInfo() {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 700) {
            this.$toast.push({ name: "Login" });
          } else if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((errr) => {
          this.$toast.clear();
          console.log(err);
        });
    },

    upLoad(file) {
      //允许文件类型
      let type = ["gif", "png", "jpg", "jpeg"];

      //允许最大文件大小 1MB
      let size = 1;

      //判断文件类型
      let fileType = file.file.type.split("/")[1];
      if (type.indexOf(fileType) === -1) {
        this.$toast(`文件类型只支持${type.join(",")}`);
        return;
      }

      let fileSize = file.file.size / 1024 / 1024;
      console.log("fileSize ==> ", fileSize);
      if (fileSize > size) {
        this.$toast(`文件允许最大不能超过${size}MB`);
        return;
      }

      let base64 = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, "");

      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType: fileType,
          serverBase64Img: base64,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            this.$router.push({ name: "Login" });
          } else if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          }

          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },
    goPage(name) {
        this.$router.push({name});
      }
  },
};
</script>

<style lang="less" scoped>
</style>